*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

@media only screen and (min-width: 112.5em) {
  html {
    font-size: 81.25%;
  }
}

@media only screen and (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}

@media only screen and (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 37.5em) {
  html {
    font-size: 43.75%;
  }
}

body {
  box-sizing: border-box;
  color: #646161e6;
  margin: 2.5rem;
  font-family: Lato, Candara, sans-serif;
}

@media only screen and (max-width: 56.25em) {
  body {
    margin: .5rem;
  }
}

@media only screen and (max-width: 37.5em) {
  body {
    margin: .2rem;
  }
}

@keyframes moveToLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }

  80% {
    opacity: .5;
    transform: translateX(1rem);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveToRight {
  0% {
    opacity: 0;
    transform: translateX(10rem);
  }

  80% {
    opacity: .5;
    transform: translateX(-1rem);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveToTop {
  0% {
    opacity: 0;
    transform: translateY(4rem);
  }

  80% {
    opacity: .5;
    transform: translateY(-1rem);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

.headText {
  text-align: center;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.headText .primaryHeading {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 3.2rem;
  margin-left: 2.2rem;
  margin-right: auto;
  font-size: 3.5rem;
  font-weight: 900;
  line-height: 8rem;
  animation: moveToLeft .85s ease-out;
  display: block;
}

@media only screen and (max-width: 37.5em) {
  .headText .primaryHeading {
    letter-spacing: 1.4rem;
    margin-left: .5rem;
  }
}

.headText .primaryHeadingSub {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: .8rem;
  font-size: 2rem;
  font-weight: 500;
  line-height: 4.5rem;
  animation: moveToRight .85s ease-out;
  display: block;
}

@media only screen and (max-width: 37.5em) {
  .headText .primaryHeadingSub {
    letter-spacing: .5rem;
    font-size: 1.5rem;
