.header {
  height: 97vh;
  clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
  backface-visibility: hidden;
  background-image: linear-gradient(to bottom right, #7ed565b3, #28b485b3), url("hero.d24b10d1.jpg");
  background-position: top;
  background-size: cover;
  position: relative;
}

@media only screen and (max-width: 37.5em) {
  .header {
    clip-path: polygon(0 0, 100% 0, 100% 58vh, 0 100%);
    backface-visibility: hidden;
    height: 70vh;
  }
}

.logoBox {
  position: absolute;
  top: 3rem;
  left: 3rem;
}

.logo {
  height: 3.5rem;
  width: 6.5rem;
}

.navigationCheckbox {
  display: none;
}

.navigationButton {
  cursor: pointer;
  height: 7rem;
  width: 7rem;
  z-index: 120;
  background-color: #fff;
  border-radius: 50%;
  position: fixed;
  top: 6rem;
  right: 6rem;
  box-shadow: 0 .2rem 1.5rem #0006;
}

@media only screen and (max-width: 56.25em) {
  .navigationButton {
    top: 3.5rem;
    right: 3.5rem;
  }
}

@media only screen and (max-width: 37.5em) {
  .navigationButton {
    top: 3rem;
    right: 3.5rem;
  }
}

.navigationBackground {
  visibility: hidden;
  height: 6rem;
  width: 6rem;
  z-index: 100;
  background-image: radial-gradient(#7ed565, #28b485);
  border-radius: 50%;
  transition: all .6s cubic-bezier(.79, -.3, .22, 1.18);
  position: fixed;
  top: 6.5rem;
  right: 6.5rem;
}

@media only screen and (max-width: 56.25em) {
  .navigationBackground {
    top: 3.5rem;
    right: 4rem;
  }
}

@media only screen and (max-width: 37.5em) {
  .navigationBackground {
    top: 3.5rem;
    right: 4rem;
  }
}

.navigationNav {
  opacity: 0;
  height: 100vh;
  width: 0;
  z-index: 110;
  transition: all .6s cubic-bezier(.79, -.3, .22, 1.18);
  position: fixed;
  top: 0;
  right: 0;
}

.navigationList {
  visibility: hidden;
  opacity: 0;
  z-index: 140;
  list-style: none;
  transition: all .6s cubic-bezier(.79, -.3, .22, 1.18);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navigationLink:link, .navigationLink:visited {
  color: #fff;
  background-image: linear-gradient(45deg, #0000 50%, #fff 50%);
  background-size: 245%;
  padding: 0 1.5rem;
  font-size: 3rem;
  font-weight: 300;
  line-height: 6.5rem;
  text-decoration: none;
  transition: all .35s;
  display: inline-block;
}

@media (max-width: 26.8em) {
  .navigationLink:link, .navigationLink:visited {
    font-size: 2rem;
  }
}

.navigationLink:hover, .navigationLink:active {
  color: #7ed565;
  background-position: 100%;
  transform: translateX(1rem);
}

.navigationCheckbox:checked ~ .navigationBackground {
  visibility: visible;
  transform: scale(75);
}

.navigationCheckbox:checked ~ .navigationNav {
  opacity: 1;
  width: 100%;
}

.navigationCheckbox:checked ~ .navigationNav .navigationList {
  opacity: 1;
  visibility: visible;
}

.navigationIcon {
  margin-top: 3.5rem;
  margin-left: 2rem;
  position: relative;
}

.navigationIcon, .navigationIcon:before, .navigationIcon:after {
  width: 3rem;
  height: .2rem;
  background-color: #333;
  display: inline-block;
}

.navigationIcon:before, .navigationIcon:after {
  content: " ";
  transition: all .2s;
  position: absolute;
  left: 0;
}

.navigationIcon:before {
  top: -.8rem;
}

.navigationIcon:after {
  top: .8rem;
}

.navigationButton:hover .navigationIcon:before {
  transform: translateY(-.2rem);
}

.navigationButton:hover .navigationIcon:after {
  transform: translateY(.2rem);
}

.navigationCheckbox:checked ~ .navigationButton .navigationIcon {
  background-color: #0000;
}

.navigationCheckbox:checked ~ .navigationButton .navigationIcon:before {
  top: 0;
  transform: rotate(135deg);
}

.navigationCheckbox:checked ~ .navigationButton .navigationIcon:after {
  top: 0;
  transform: rotate(-135deg);
}

.btn, .btn:link, .btn:visited {
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  text-decoration: none;
}

.btn:hover {
  transform: translateY(-.3rem);
  box-shadow: 0 1rem 1.4rem #0003;
}

.btn:active {
  outline: none;
  transform: translateY(-.1rem);
  box-shadow: 0 .6rem 1rem #00000067;
}

.btn:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  border-radius: 4rem;
  transition: all .3s;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
}

.btn:hover:after {
  opacity: 0;
  transform: scaleX(1.3)scaleY(1.5);
}

.btn-white {
  color: #00000096;
  background-color: #fff;
  border-radius: 4rem;
  margin-top: 5rem;
  padding: 1.3rem 2.5rem;
  font-size: 1.5rem;
  display: inline-block;
  position: relative;
}

.btn-white:after {
  background-color: #fff;
}

.btn-white.animated-btn {
  animation: moveToTop .5s ease-out .9s backwards;
}

.btn-green {
  color: #fff;
  background-color: #55c57a;
  border-radius: 4rem;
  margin-top: 5rem;
  padding: 1.2rem 2.5rem;
  font-size: 1.5rem;
  font-weight: 300;
  display: inline-block;
  position: relative;
}

.btn-green:after {
  background-color: #55c57a;
}

.btn-green-1 {
  margin-top: 1rem;
}

.btn-green-2 {
  margin-top: 0;
  margin-left: 2rem;
}

.btn-text:link, .btn-text:visited {
  color: #55c57a;
  border-bottom: 1px solid #55c57a;
  padding: .3rem;
  font-size: 1.5rem;
  text-decoration: none;
  transition: all .13s;
  display: inline-block;
}

.btn-text:hover {
  color: #fff;
  background-color: #55c57a;
  transform: translateY(-.2rem);
  box-shadow: .2rem .3rem 2rem #0000008c;
}

.btn-text:active {
  transform: translateY(0);
  box-shadow: .1rem .3rem 1rem #000000a6;
}

