  padding: 6rem 6rem 6rem 8rem;
  font-size: 1.5rem;
  transition: all .45s;
  transform: skewX(-12deg);
  box-shadow: 0 1rem 6rem #00000059;
}

.storiesShape {
  width: 15rem;
  height: 15rem;
  float: left;
  -webkit-shape-outside: circle(50%);
  shape-outside: circle(50%);
  backface-visibility: hidden;
  border-radius: 10rem;
  position: relative;
  transform: translateX(-2rem)skewX(12deg);
}

.storiesImage {
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 10rem;
  transition: all .45s;
  transform: translateX(-15%)scale(1.1);
}

.storiesText {
