}

.stories:hover .storiesCaption {
  opacity: 1;
  backface-visibility: hidden;
  transform: translate(-75%, -50%);
}

.stories:hover .storiesImage {
  filter: blur(.3rem) brightness(90%);
  transform: translateX(-15%)scale(1.05);
